import {Injectable, OnDestroy} from '@angular/core';
import { NzNotificationService } from 'ng-zorro-antd';
const io: any = require('../../assets/plugins/socket/socket.io.js');
declare var $: any;
declare var window: any;
@Injectable()

export class MessageHub implements OnDestroy{
    MessageHubUrl: string;
    MessageHubObj: any;
    user: any;
    constructor(private notice: NzNotificationService) {
        // 配置服务器地址;
        this.MessageHubUrl = window.config.messageHubUrl;
        // 当前用户
        this.user = window.config.messageHubUser;
        // 建立消息服务连接
        this.MessageHubObj = io.connect(this.MessageHubUrl);
        let isError = false;
        let isTimeout = false;
        this.MessageHubObj.on("connect_error",(err)=>{
            if(!isError) {
                this.notice.create('error','消息服务反馈：','建立连接失败', {nzDuration: 0})
                isError = true;
            }
        });
        this.MessageHubObj.on("connect_timeout",(err)=>{
            if(isTimeout) {
                this.notice.create('error','消息服务反馈：','建立连接超时', {nzDuration: 0})
                isTimeout = true;
            }
        });
        this.authentication();
    }
    ngOnDestroy() {
        // 断开与消息服务的连接
        this.MessageHubObj.disconnect();
    }
    // 响应处理
    dealWith(res: any) {
        if(res.code === 110) {
            this.notice.create(res.code===200?'success':'error','消息服务反馈','正在重连消息服务，请登录成功后再次操作')
            this.authentication();
        }else {
            this.notice.create(res.code===200?'success':'error','消息服务反馈',res.message)
        }
    }
    // 注册当前用户
    authentication() {
        this.MessageHubObj.emit('authentication', JSON.stringify(this.user), (res: any) => {
            this.dealWith(res);
        });
    }
    // 监听会话列表
    listenConnectionList(callback?: any) {
        this.MessageHubObj.on('getConnections', data => {
            const res = JSON.parse(data);
            callback(res);
        });
    }
    // 监听池列表
    listenPoolList(calback: any) {
        this.MessageHubObj.on('listenPoolList', data => {
            const res = JSON.parse(data);
            calback(res);
        });
    }
    // 监听池的消息列表
    listenPool(poolName: any, callback?: any) {
        this.MessageHubObj.emit('listenPool', poolName, callback)
    }
    // 取消监听池的消息列表
    unlistenPool(poolName: any, callback?: any) {
        this.MessageHubObj.emit('unlistenPool', poolName, callback);
    }
    // 监听池返回的数据入口
    receivePoolInfo(calback: any) {
        this.MessageHubObj.on('receivePoolInfo', data => {
            const res = JSON.parse(data);
            calback(res);
        });
    }
    // 创建池
    createPool(poolObj: any, callback: any) {
        this.MessageHubObj.emit('createPool', JSON.stringify(poolObj), (res: any) => {
            this.dealWith(res);
            callback(res);
        });
    }
    // 删除池
    destroyPool(poolName: any, callback?: any) {
        this.MessageHubObj.emit('destroyPool', poolName, (res: any) => {
            this.dealWith(res);
            callback(res);
        });
    }
    // 创建池的消息
    createMessage(data: any, callback?: any) {
        this.MessageHubObj.emit('createMessage', JSON.stringify(data), (res: any) => {
            this.dealWith(res);
            callback(res);
        });
    }
    // 修改池的消息
    updateMessage(data: any, callback?: any) {
        this.MessageHubObj.emit('updateMessage', JSON.stringify(data), (res: any) => {
            this.dealWith(res);
            callback(res);
        });
    }
    // 删除池的消息
    deleteMessage(data: any, callback?: any) {
        console.log(data);
        this.MessageHubObj.emit('deleteMessage', JSON.stringify(data), (res: any) => {
            this.dealWith(res);
            callback(res);
        });
    }
    // 清空池的所有消息
    flushPool(poolName: any, callback?: any) {
        this.MessageHubObj.emit('flushPool', poolName, (res: any) => {
            this.dealWith(res);
            callback(res);
        });
    }
    // 生成GUID唯一编码
    createGuid() {
        return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
            var r = Math.random()*16|0, v = c == 'x' ? r : (r&0x3|0x8);
            return v.toString(16);
        });
    }
};



